*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {

  font-family: Arial, sans-serif;
  background-color: #e8ddc1;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.game-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1440px;
  height: 100vh;
  background-color: #fff8db;
  border-radius: 15px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  padding: 10px;
  overflow: hidden;
}

.game-board {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-gap: 10px;
  overflow-y: auto;
  padding: 10px;
  background-color: #f7e6b3;
  border-radius: 10px;
  flex-grow: 1;
}

.animal-item {
  text-align: center;
}

.animal-item img {
  width: 100px;
  height: 100px;
  object-fit: contain;
}

.side-panel {
  background-color: #7c5737;
  color: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.timer, .score {
  background-color: #f7a760;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 1.5rem;
  margin: 10px 0;
}

.current-animal img {
  width: 150px;
  height: 150px;
  margin-bottom: 10px;
}
.gameOver{
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.64);
  display: flex;
  align-items: center;
  justify-content: center;

}
.modal{
  max-width: 350px;
  width: 100%;
  background: rgba(39, 15, 15, 0.6);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px 0;
  padding: 10px 0;
  color: white;
  button{
    background: transparent;
    border: 1px solid white;
    padding: 10px 20px;
    font-size: 20px;
    color: white;
  }
}
/* Responsive Design */
@media (min-width: 768px) {
  .game-container {
    flex-direction: row;
  }

  .game-board {
    width: 70%;
    height: auto;
    overflow-y: auto;
  }

  .side-panel {
    width: 30%;
    height: 100%;
    margin-top: 0;
  }
}

@media (max-width: 767px) {
  .game-board {
    grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  }

  .animal-item img {
    width: 80px;
    height: 80px;
  }

  .current-animal img {
    width: 100px;
    height: 100px;
  }
}

@media (max-width: 480px) {
  .game-board {
    grid-template-columns: repeat(2, 1fr);
  }

  .animal-item img {
    width: 60px;
    height: 60px;
  }

  .current-animal img {
    width: 80px;
    height: 80px;
  }

  .timer, .score {
    font-size: 1.2rem;
  }
}